@keyframes bounce {
	0% {
		transform: translateY(0);
	}

	50% {
		transform: translateY(5px);
	}

	100% {
		transform: translateY(0);
	}
}

@keyframes moveSide {
	0%,
	30% {
		transform: translateX(0);
	}

	10% {
		transform: translateX(-5px);
	}

	20% {
		transform: translateX(5px);
	}
}

@keyframes moveUp {
	0% {
		transform: translateY(0);
	}

	25% {
		transform: translateY(5px);
	}

	50% {
		transform: translateY(0);
	}

	75% {
		transform: translateY(-5px);
	}

	100% {
		transform: translateY(0);
	}
}

@keyframes scaleDown {
	0%,
	6% {
		transform: scale(1);
	}

	3% {
		transform: scale(0.8);
	}
}

@keyframes rotate360 {
	0% {
		transform: rotate(0);
	}

	100% {
		transform: rotate(360deg);
	}
}

@keyframes blinkOpacity {
	0% {
		opacity: 1;
	}

	50% {
		opacity: 0.5;
	}

	100% {
		opacity: 1;
	}
}